:root {
  --clr-neutral-100: #fff;
  --clr-primary-200: #fd5c6a;
  --clr-primary-400: #9065fe;

  --ff-primary: 'Red Rose', serif;
  --ff-accent: 'Actor', serif;
}
body,
h1,
h2,
p {
  margin: 0;
}

h1,
h2 {
  margin-bottom: 1rem;
}

h1 {
  font-size: 5.4rem;
  letter-spacing: -1px;
}

h2 {
  font-size: 2.5rem;
  font-weight: normal;
}

@media (max-width: 1200px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 2rem;
  }
}

@media (max-width: 540px) {
  h1 {
    font-size: 2.4rem;
  }

  h2 {
    font-size: 1.2rem;
  }
}

p {
  font-size: 1rem;
  padding: 0.625rem;
}

button {
  border-radius: 1.5rem;
  background: none;
  border: 1px solid white;
  color: white;
  font-size: 1rem;
}

svg {
  height: 100%;
  width: 100%;
}

@keyframes change-color {
  0% {
    background: var(--clr-primary-200);
  }
  100% {
    background: var(--clr-primary-400);
  }
}
