@import url(https://fonts.googleapis.com/css?family=Actor:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Oxyge&family=Red+Rose);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --clr-neutral-100: #fff;
  --clr-primary-200: #fd5c6a;
  --clr-primary-400: #9065fe;

  --ff-primary: 'Red Rose', serif;
  --ff-accent: 'Actor', serif;
}
body,
h1,
h2,
p {
  margin: 0;
}

h1,
h2 {
  margin-bottom: 1rem;
}

h1 {
  font-size: 5.4rem;
  letter-spacing: -1px;
}

h2 {
  font-size: 2.5rem;
  font-weight: normal;
}

@media (max-width: 1200px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 2rem;
  }
}

@media (max-width: 540px) {
  h1 {
    font-size: 2.4rem;
  }

  h2 {
    font-size: 1.2rem;
  }
}

p {
  font-size: 1rem;
  padding: 0.625rem;
}

button {
  border-radius: 1.5rem;
  background: none;
  border: 1px solid white;
  color: white;
  font-size: 1rem;
}

svg {
  height: 100%;
  width: 100%;
}

@-webkit-keyframes change-color {
  0% {
    background: #fd5c6a;
    background: var(--clr-primary-200);
  }
  100% {
    background: #9065fe;
    background: var(--clr-primary-400);
  }
}

@keyframes change-color {
  0% {
    background: #fd5c6a;
    background: var(--clr-primary-200);
  }
  100% {
    background: #9065fe;
    background: var(--clr-primary-400);
  }
}

